@font-face {
  font-family: "PTSans";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("PTSans Regular"), url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
}
@font-face {
  font-family: "PTSans";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: local("PTSans Bold"), url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
}
@font-face {
  font-family: "Exo 2";
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  src: local("Exo 2 SemiBold"), url("../fonts/exo2semibold.woff2") format("woff2"), url("../fonts/exo2semibold.woff") format("woff");
}
body {
  min-width: 320px;
  margin: 0;
}
img {
  max-width: 100%;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page-body {
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: #000000;
  margin: 0 auto;
  position: relative;
  min-height: 100%;
}
.container {
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    width: 690px;
    margin: 0 auto;
    padding: 0 39px;
  }
}
@media (min-width: 1440px) {
  .container {
    width: 1240px;
    margin: 0 auto;
    padding: 0 100px;
  }
}
.page-header__wrapper {
  padding: 0 16px;
  display: flex;
  align-items: center;
  flex-direction: column;
  z-index: 1;
  background-color: #0B7572;
}
.page-header__container {
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 75px 0 132px;
}
.page-header__title {
  font-family: "Exo 2", "Arial", sans-serif;
  font-weight: 600;
  font-size: 26px;
  line-height: 35px;
  color: #ffffff;
  margin: 0;
}
.page-header__slogan {
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 23px;
  color: #ffffff;
  text-align: left;
  margin: 27px 0 0;
}
.page-header__slogan br {
  display: none;
}
.page-header__slogan-none {
  display: none;
}
.page-header__link {
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: 700;
  font-size: 13px;
  line-height: 23px;
  color: #ffffff;
  text-decoration: none;
  position: relative;
  z-index: 1;
  margin-top: 30px;
}
.page-header__link::after {
  content: "";
  position: absolute;
  left: 0;
  top: 22px;
  width: 98px;
  height: 3px;
  background-color: #FFCE50;
  z-index: 2;
}
.page-header__link:hover {
  color: #000000;
}
.page-header__link:active {
  color: #9F9F9F;
}
.page-header__link:hover::after {
  content: "";
  position: absolute;
  left: 0;
  top: 22px;
  width: 110px;
  height: 3px;
  background-color: #000000;
  z-index: 2;
}
.page-header__link:active::after {
  content: "";
  position: absolute;
  left: 0;
  top: 22px;
  width: 110px;
  height: 3px;
  background-color: #9F9F9F;
  z-index: 2;
}
.page-header__image-holder {
  margin: 46px 0;
}
.page-header__image {
  width: 345px;
  height: 253px;
  background-color: #6B6B6C;
  border-radius: 15px;
}
@media (min-width: 768px) {
  .page-header--preview {
    position: relative;
    z-index: 4;
  }
  .page-header--preview::before {
    content: "";
    position: absolute;
    display: block;
    width: 23%;
    min-height: 743px;
    top: 0;
    right: 1px;
    left: inherit;
    z-index: -2;
    background-color: #0B7572;
    background-position: 0%;
    background-repeat: no-repeat;
    background-size: cover;
  }
  .page-header__wrapper {
    padding: 0;
    display: flex;
    align-items: center;
    flex-direction: column;
    z-index: 1;
    background: none;
  }
  .page-header__container {
    padding: 80px 0 40px;
  }
  .page-header__title {
    font-family: "Exo 2", "Arial", sans-serif;
    font-weight: 600;
    font-size: 44px;
    line-height: 55px;
    color: #363738;
    margin: 0 100px 0 0;
  }
  .page-header__slogan {
    font-family: "PT Sans", "Arial", sans-serif;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #363738;
    text-align: left;
  }
  .page-header__slogan br,
  .page-header__slogan-none {
    display: block;
  }
  .page-header__text {
    margin: 61px 0 0 0;
  }
  .page-header__link {
    color: #363738;
  }
  .page-header__image-holder {
    margin: 56px 0 0;
    width: 690px;
    height: 314px;
    background-image: url("../img/image-cars-tablet.png");
    border-radius: 15px;
  }
  .page-header__image {
    display: none;
  }
}
@media (min-width: 1440px) {
  .page-header__wrapper {
    width: 1240px;
    display: flex;
    padding: 120px 100px;
  }
  .page-header__container {
    display: flex;
    flex-direction: row;
    align-items: none;
    padding: 0;
  }
  .page-header--preview {
    position: relative;
    z-index: 4;
  }
  .page-header--preview::before {
    content: "";
    position: absolute;
    display: block;
    width: 40%;
    min-height: 743px;
    top: 0;
    right: 1px;
    left: inherit;
    z-index: -2;
    background-color: #0B7572;
    background-position: 0%;
    background-repeat: no-repeat;
    background-size: cover;
  }
  .page-header__title {
    margin-right: 0;
  }
  .page-header__image-holder {
    margin: 0;
    background-image: none;
    margin-right: 0;
    width: 635px;
  }
  .page-header__image {
    position: relative;
    top: -60px;
    display: block;
    width: 635px;
    height: 505px;
    margin: 0;
    padding: 0;
  }
}
.page-main__wrapper {
  padding: 0 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.page-main__list {
  background-color: #ffffff;
  padding: 0;
  margin: 0;
  list-style: none;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
  border-radius: 20px;
  position: relative;
  top: -125px;
  left: 0;
}
.page-main__item {
  padding: 24px 27px 24px 32px;
  margin: 0;
}
.page-main__item--goods svg,
.page-main__item--delivery svg,
.page-main__item--manufacturer svg {
  width: 68px;
  height: 68px;
}
.page-main__title {
  font-family: "Exo 2", "Arial", sans-serif;
  font-weight: 600;
  font-style: normal;
  font-size: 18px;
  line-height: 24px;
  color: #363738;
  margin: 13px 0 12px 0;
}
.page-main__slogan {
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 23px;
  color: #6B6B6C;
  text-align: left;
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .page-main__wrapper {
    padding: 0;
  }
  .page-main__list {
    box-shadow: none;
    border-radius: none;
    position: relative;
    top: 20px;
  }
  .page-main__item {
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
    border-radius: 20px;
    display: flex;
    gap: 32px;
    justify-content: space-around;
    margin-bottom: 16px;
  }
  .page-main__title {
    font-size: 20px;
    line-height: 27px;
  }
  .page-main__slogan {
    margin-right: 150px;
  }
  .page-main__slogan br {
    display: none;
  }
  .page-main__item--goods svg,
  .page-main__item--delivery svg,
  .page-main__item--manufacturer svg {
    width: 92px;
    height: 92px;
  }
}
@media (min-width: 1440px) {
  .page-main__wrapper {
    width: 1240px;
    padding: 93px 100px 50px 100px;
  }
  .page-main__list {
    position: relative;
    top: 0;
    z-index: 5;
    display: flex;
    flex-direction: row;
    gap: 0;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
  }
  .page-main__item {
    box-shadow: none;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    gap: 32px;
    justify-content: space-around;
    margin-bottom: 0;
    padding: 40px 100px 56px 80px;
    position: relative;
  }
  .page-main__item::after {
    content: "";
    position: absolute;
    top: 200px;
    right: -130px;
    width: 261px;
    height: 1px;
    background-color: #CBCBCD;
    background-repeat: no-repeat;
    transform: rotate(90deg);
  }
  .page-main__item:last-child::after {
    display: none;
  }
  .page-main__title {
    width: 228px;
  }
  .page-main__slogan {
    margin-right: 0;
  }
}
.scope-work__container {
  padding: 60px 0;
}
.scope-work__lead {
  font-family: "Exo 2", "Arial", sans-serif;
  font-weight: 600;
  font-size: 28px;
  line-height: 35px;
  color: #363738;
  margin: 0;
}
.scope-work__slogan {
  display: none;
}
.scope-work__list {
  list-style: none;
  padding: 0;
  margin: 32px 0 0 0;
}
.scope-work__item {
  padding: 24px 27px 32px 16px;
  box-shadow: 0 3px 15px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  margin-bottom: 10px;
  display: flex;
  gap: 30px;
}
.scope-work__item--checked {
  border: 1px solid #0B7572;
}
.scope-work__title {
  font-family: "Exo 2", "Arial", sans-serif;
  font-weight: 600;
  font-size: 18px;
  color: #363738;
  margin: 0;
}
.scope-work__text {
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: #6B6B6C;
  margin: 10px 0 0 0;
}
.scope-work__checkmark {
  position: absolute;
  width: 24px;
  height: 24px;
  background: #ffffff;
  border: 1px solid #CBCBCD;
  box-sizing: border-box;
  box-shadow: inset 0 4px 4px rgba(0, 0, 0, 0.15);
  border-radius: 50%;
  margin-top: 3px;
}
.scope-work__checkmark:checked::after {
  content: "";
  position: absolute;
  top: 10px;
  left: 0;
  width: 10px;
  height: 10px;
  background-color: #0B7572;
  border-radius: 50%;
}
.scope-work__box {
  display: none;
}
@media (min-width: 768px) {
  .scope-work__container {
    padding: 50px 0;
  }
  .scope-work__list {
    margin-top: 48px;
  }
  .scope-work__item {
    padding: 40px 127px 48px 40px;
    gap: 60px;
    margin-bottom: 12px;
  }
  .scope-work__lead {
    font-size: 44px;
    line-height: 55px;
  }
  .scope-work__slogan {
    display: block;
    font-size: 16px;
    color: #6B6B6C;
    margin: 32px 0 0;
  }
  .scope-work__text {
    font-family: "PT Sans", "Arial", sans-serif;
    font-size: 16px;
    line-height: 24px;
  }
  .scope-work__text br {
    display: none;
  }
}
@media (min-width: 1440px) {
  .scope-work__container {
    padding: 0;
    display: flex;
    flex-direction: column;
    justify-content: left;
    margin-top: 100px;
  }
  .scope-work__holder {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 30px;
  }
  .scope-work__lead {
    text-align: left;
  }
  .scope-work__slogan {
    line-height: 24px;
  }
  .scope-work__item {
    width: 607px;
    padding: 40px 40px 48px 40px;
    position: relative;
  }
  .scope-work__item::after {
    content: "";
    position: absolute;
    bottom: 0;
    right: 0;
    background-repeat: no-repeat;
    width: 0;
    height: 0;
    border-bottom: 70px solid #E9E9EC;
    border-left: 180px solid transparent;
    z-index: -1;
  }
  .scope-work__box {
    display: block;
    padding: 40px 40px 48px 40px;
  }
  .scope-work__heading {
    font-family: "Exo 2", "Arial", sans-serif;
    font-weight: 600;
    font-size: 20px;
    line-height: 30px;
    color: #363738;
    margin: 0;
  }
  .scope-work__desc {
    font-family: "Exo 2", "Arial", sans-serif;
    font-weight: 600;
    font-size: 20px;
    position: relative;
  }
  .scope-work__desc::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: -39px;
    width: 281px;
    height: 3px;
    background-color: #FFCE50;
    z-index: 2;
  }
  .scope-work__desc-count {
    font-size: 54px;
  }
  .scope-work__text {
    margin-right: 50px;
  }
}
